html {
  box-sizing: border-box;
}

.quote-container{
  width: auto;
  max-width: 900px;
  padding: 20px 30px;
  border-radius: 10px;
  background-color: rgba(255,255,255, .4);
  box-shadow: 0 10px 10px 10px rgba(0,0,0,.2);
}

.quote-text{
  font-size: 2.75rem;
}

.long-quote {
  font-size: 2rem;
}

.faQuoteLeft{
  font-size: 4rem;
}

.quote-author{
  margin-top: 15px;
  font-size: 2rem;
  font-weight: 400;
  font-style: italic;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button-container{
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
}

button {
  cursor: pointer;
  font-size: 1.2rem;
  height: 2.5rem;
  border: none;
  border-radius: 10px;
  color: white;
  background: #333;
  outline: none;
  padding: .5rem 1.8rem;
  box-shadow: 0 .3rem rgba(121, 121, 121, .65);
}

button :hover{
  filter: brightness(110%);
}

button:active{
  transform: translate(0, .3rem);
  box-shadow: 0 .1rem rgba(255, 255, 255, .65);

}

.twitter-button:hover {
  color: #38a1f3;
}

.fa-twitter {
  font-size: 1.5rem;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #333; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/*Media Query*/

@media screen and (max-width: 1000px){
  .quote-container{
    margin: auto 10px;
  }
  .quote-text{
    font-size: 2.5rem;
  }
}